import React, { useEffect, useState } from 'react';
import { WhiteSpace, InputItem, Toast } from 'antd-mobile';
import { useHistory } from 'react-router-dom';
import { useLazyQuery, useMutation } from '@apollo/client';
import { USER_LOGIN, USER_SHOW } from '@/helpers/APIHelper';
import storage from '@/utils/storage';
import StorageKey from '@/constants/StorageKey';
import md5 from 'crypto-js/md5';
import { LoginFormValues, UserInfo } from './types';
import { emailReg, pwdReg } from './regs';
import {
  Page,
  PageTitle,
  PrimaryButton,
  OthersBox,
  OthersItem,
} from './styles';

const Login: React.FC = () => {
  const history = useHistory();
  const [formValues, setFormValues] = useState<LoginFormValues>({}); // 表单值
  const [loginReq, { loading }] = useMutation(USER_LOGIN, {
    onCompleted: (loginRes) => loginSuccess(loginRes),
    onError: () => {
      setTimeout(() => Toast.show('登录失败'), 100);
    },
  });
  const [getUserInfo] = useLazyQuery(USER_SHOW, {
    fetchPolicy: 'cache-and-network',
    onCompleted: (data) => getUserInfoSuccess(data),
  });

  // 登录成功后操作
  const loginSuccess = (data: { signIn: { token: string } }): void => {
    console.log(data);
    storage.setLocal(StorageKey.TOKEN, data?.signIn?.token || '');
    // 查询用户信息
    getUserInfo();
  };
  // 获取用户信息
  const getUserInfoSuccess = (data: { getUserInfo: UserInfo }): void => {
    console.log(data);
    const { nickName, artScience, ejuSubject } = data.getUserInfo;
    const noArtScience = !artScience && typeof artScience !== 'number';
    const noEjuSubject = !ejuSubject && typeof ejuSubject !== 'number';
    if (!nickName || noArtScience || noEjuSubject) {
      history.replace('/complete');
    } else {
      history.replace('/');
    }
  };
  // 表单校验
  const checkValues = (): boolean => {
    const { email, password } = formValues;
    switch (false) {
      case !!email:
        Toast.info('请输入电子邮箱');
        break;
      case emailReg.test(email || ''):
        Toast.info('请输入正确的电子邮箱格式');
        break;
      case !!password:
        Toast.info('请输入账号密码');
        break;
      case pwdReg.test(password || ''):
        Toast.info('请输入8-24位包含大小写字母数字及特殊字符的密码');
        break;
      default:
        return true;
    }
    return false;
  };
  // 提交登录
  const handleLogin = (): void => {
    if (checkValues()) {
      const { email, password } = formValues;
      console.log({ variables: { email, password } });
      loginReq({
        variables: { email, password: md5(password || '').toString() },
      });
    }
  };
  // 表单值改变
  const handleChange = (value: string, type: string): void => {
    setFormValues((vals) => ({ ...vals, [type]: value }));
  };

  // 接口请求状态
  useEffect(() => {
    loading && Toast.loading('登录中', 0);
    !loading && Toast.hide();
  }, [loading]);
  return (
    <Page>
      <PageTitle>欢迎来到菓子题库</PageTitle>
      <InputItem
        placeholder="请输入邮箱/用户名"
        onChange={(val): void => handleChange(val, 'email')}
        name="email"
      />
      <WhiteSpace size="lg" />
      <InputItem
        placeholder="请输入密码"
        type="password"
        name="password"
        onChange={(val): void => handleChange(val, 'password')}
      />
      <PrimaryButton type="primary" mt="286px" onClick={handleLogin}>
        登录
      </PrimaryButton>
      <OthersBox>
        <OthersItem onClick={(): void => history.push('/register')}>
          新用户注册
        </OthersItem>
        <OthersItem>忘记密码</OthersItem>
      </OthersBox>
    </Page>
  );
};

export default Login;
